:root {
  --color-text: #000;
  --color-bg: #fff;
  --color-link: #000;
  --color-link-hover: #858585;
  --color-nav-bg: #fff;
  --color-nav-hover: #858585;
  --color-cart-bg: #000;
  --color-cart: #fff;
  --color-details-hover: #858585;
  min-height: 100vh;
  color: #57585c;
  color: var(--color-text);
  background-color: #fff;
  background-color: var(--color-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Futura, Trebuchet MS, Arial, sans-serif;
}

a:hover, a:focus {
  color: var(--color-link-hover);
}

.hidden {
  width: 0;
  height: 0;
  pointer-events: none;
  overflow: hidden;
}

.icon {
  width: 1.5em;
  height: 1.5em;
  fill: currentColor;
  margin: 0 auto;
  display: block;
}

.content {
  text-align: center;
}

.info {
  max-width: 260px;
  margin: 0 1rem;
  font-weight: bold;
  display: block;
}

.page-header__title {
  margin: 0;
  font-size: 1rem;
}

.page-header__title-inner {
  margin: 0 .5rem 0 0;
  padding: 0 1rem 0 0;
  line-height: 1.25;
  position: relative;
}

.page-header__title-inner:after {
  content: "";
  width: 3px;
  height: 1.25rem;
  background: currentColor;
  position: absolute;
  right: 0;
}

.social {
  display: none;
}

.social__item {
  margin: 0 .25rem;
  font-size: .75rem;
  display: block;
}

.slideshow {
  width: 100%;
  margin-top: 1rem;
  position: relative;
}

.slideshow:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 0;
  background: rgba(0, 0, 0, .4);
  transition: opacity .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.slideshow--details:after {
  opacity: 1;
}

.slide {
  width: 100%;
  height: 50vh;
  top: 0;
  left: 0;
}

.slide--current {
  opacity: 1;
  z-index: 999;
  pointer-events: auto;
  position: relative;
}

.slide__wrap {
  width: 100%;
  height: 50vh;
  position: relative;
  overflow: hidden;
}

.slide__bg, .slide__img {
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: #fff;
  background-position: 50%;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.slide__bg {
  filter: grayscale() brightness(.7);
}

.slide__title-wrap {
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 2rem 1rem 0;
  position: absolute;
  top: 0;
  left: 0;
}

.slide__title {
  white-space: pre;
  text-align: left;
  margin: 0;
  font-size: 3.5rem;
  line-height: .9;
}

.slide__title span {
  display: inline-block;
}

.slide__subtitle {
  text-align: left;
  font-size: 1.15rem;
  font-weight: normal;
}

.boxnav {
  z-index: 1000;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

.boxnav__item {
  background: var(--color-nav-bg);
  height: 4rem;
  border: 0;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.boxnav__item--prev:hover, .boxnav__item--next:hover {
  color: var(--color-nav-hover);
}

.boxnav__item:focus {
  outline: none;
}

.boxnav__label {
  padding: 0 .5rem;
  font-size: 1.15rem;
  position: relative;
}

.boxnav__label--total:before {
  content: "";
  width: 2px;
  height: 1.45rem;
  background: #000;
  position: absolute;
  top: 0;
  right: 100%;
  transform: rotate(22.5deg);
}

.icon--caret-rot {
  transform: rotate(180deg);
}

.details-wrap {
  pointer-events: none;
  width: 100%;
  z-index: 10000;
  position: relative;
}

.details {
  width: 100%;
  grid-auto-rows: 80px;
  display: grid;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
}

.details-wrap--open .details--current {
  pointer-events: auto;
}

.details__item {
  overflow: hidden;
}

.details__item--addtocart {
  grid-row: 4;
}

.details__inner {
  width: 100%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  display: flex;
  transform: translate3d(100%, 0, 0);
}

.details__item-img {
  display: none;
}

.details__inner--sizes {
  background: #d4d4d4;
}

.details__size {
  cursor: pointer;
  position: relative;
}

.details__size--selected:before {
  content: "";
  width: 2rem;
  height: 2rem;
  border: 2px solid #333;
  border-radius: 50%;
  margin: -1rem 0 0 -1rem;
  position: absolute;
  top: 50%;
  left: 50%;
}

.details__size--disabled {
  color: #d42d2d;
  cursor: not-allowed;
}

.details__inner--price {
  color: #020202;
  background: #6ff8ed;
  font-size: 1.55rem;
}

.details__inner--colors {
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  background: rgba(255, 255, 255, .008);
}

.details__color {
  width: 1.5rem;
  height: 1.5rem;
  color: rgba(0, 0, 0, 0);
  cursor: pointer;
  border-radius: 50%;
  align-self: center;
  justify-self: center;
  overflow: hidden;
}

.details__color--WhiteBlack {
  background: linear-gradient(90deg, #fff 20%, #000 100%);
}

.details__color--BeigeGray {
  background: linear-gradient(90deg, #f0be8f 20%, #5c5454 100%);
}

.details__color--BlueBlack {
  background: linear-gradient(90deg, #1086f5 20%, #000 100%);
}

.details__color--PinkBlack {
  background: linear-gradient(90deg, #8610f5 20%, #000 100%);
}

.details__color--WhiteRedBlue {
  background: linear-gradient(90deg, #fff 10%, #790909 45%, #00d4ff 100%);
}

.details__color--WhiteGreenBlack {
  background: linear-gradient(90deg, #fff 10%, #04e98a 45%, #000 100%);
}

.details__color--red {
  background: red;
}

.details__color--green {
  background: #32f8a5;
}

.details__color--sky {
  background: #add8e6;
}

.details__inner--grid {
  grid-template-columns: repeat(3, 1fr);
  padding: 1rem;
  display: grid;
}

.action {
  background: none;
  border: 0;
  flex: 1;
  align-self: stretch;
  padding: 0;
}

.action--addtocart {
  color: var(--color-cart-bg);
  background: #6ff8ed;
}

.action--close {
  background: var(--color-cart-bg);
  color: #6ff8ed;
  text-transform: lowercase;
}

.action--details {
  z-index: 3000;
  width: 100%;
  color: #fff;
  background: #000;
